import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import reportWebVitals from './reportWebVitals';
import Home from "./Component/Home/Home";
import Htj from './Component/Htj/Htj';
import Htw from './Component/Htw/Htw';
import Tts from './Component/Tts/Tts';
import { BrowserRouter as Router } from 'react-router-dom'
import { Switch, Route  } from 'react-router-dom'

ReactDOM.render(
  <React.StrictMode>
    <Router>
      <Switch>
        <Route exact path="/" render={() => <Home url={window.location.href} />} />
        <Route path="/?id=id" render={() => <Home url={window.location.href} />} />
        <Route path="/join" component={Htj} />
        <Route path="/works" component={Htw} />
        <Route path="/sponsors" component={Tts} />
      </Switch>
    </Router>
  </React.StrictMode>,
  document.getElementById('root')
);

reportWebVitals();
