import React, { Component } from 'react'
import { NavLink } from 'react-router-dom'

class Htj extends Component {
  constructor(props) {
    super(props)
    this.state = {}
  }

  render() {
    return (
      <>
        <main id="App">
          <section className="section-top">
          <NavLink to="/">
            <a >
              <img src="/img/logo-desktop.png" className="top-heading w16" />
              <img src="/img/logo-desktop.png" className="top-heading-mobile" />
            </a>
            </NavLink>
          </section>

          <section className="section">
            <div className="page">
              <h3>How to join</h3>
              <p>To join you need to use one of the following Dapp wallets.</p>
              <p>
                To connect with the smart contract, please download, set up, and
                fund your preferred wallet from the instructions below.
              </p>
              <p>
                *it is mandatory to use a referral link 1st time depositing to
                Tron Algo smart contract, please get back to the person that
                introduced you to Tron Algo.
              </p>
              <p>PHONE WALLET -</p>

              <p>
                KLEVER is a secure p2p crypto wallet and exchange for TRON
                (TRX)
              </p>
              <ol>
                <li>Download and set up KLEVER to phone</li>
                <li>Fund KLEVER wallet with TRX</li>
                <li>Go to Dapp browser in wallet and enter the ref link</li>
                <li>select amount to deposit and click join</li>
              </ol>
              <hr />
              <p>What is TRONLINK?</p>
              <p>
                TRONLINK is Available as a browser extension and as a mobile
                app, TronLink equips you with a key vault, secure login, and
                token wallet—everything you need to manage your digital assets.
              </p>
              <p>
                TronLink generates passwords and keys on your device, so only
                you have access to your accounts and data
              </p>

              <p>How to connect TRONLINK to Tron Algo smart contract?</p>

              <p>*first time joining TRON ALGO, follow these steps</p>
              <p>
                Copy ref link from sponsor
                <br />
                Enter ref link into your pc browser with TRONLINK extension
                <br />
                Select the amount you wish to deposit up to 100.000 TRX
                <br />
                Click join and confirm transaction in wallet
              </p>

              <br />
              <div className="panel-info-item">
                  <NavLink to="/">
                <a className="btn">
                  Back
                </a>
                </NavLink>
              </div>
            </div>
          </section>
        </main>

        <footer>
          <div className="footer-content">
            <div>
              <button className="button">
                <div className="button-content">Official Channel</div>
                <div className="button-image">
                  <img src="/img/button.png" />
                </div>
              </button>
            </div>

            <div>
              <button className="button">
                <div className="button-content">Support Team</div>
                <div className="button-image">
                  <img src="/img/button.png" />
                </div>
              </button>
            </div>
          </div>
        </footer>
      </>
    )
  }
}

export default Htj
