import React, { Component } from 'react'
import { NavLink } from 'react-router-dom'

class Tts extends Component {
  constructor(props) {
    super(props)
    this.state = {}
  }
  render() {
    return (
      <>
        <main id="App">
          <section class="section-top">
              <NavLink to="/">
            <a>
              <img src="/img/logo-desktop.png" class="top-heading w16" />
              <img src="/img/logo-desktop.png" class="top-heading-mobile" />
            </a>
            </NavLink>
          </section>

          <section class="section">
            <div class="container">
              <div class="panel-info">
                <h3>Top 4 sponsors</h3>
                <div>
                  <table
                    style={{
                      fontSize: '13px',
                      lineHeight: '2.5',
                      width: '100%',
                      textTransform: 'none',
                      color:'white'
                    }}
                  >
                    <thead>
                      <tr>
                        <th width="60%">Address</th>
                        <th width="20%">TRX</th>
                        <th width="20%">Income</th>
                      </tr>
                    </thead>
                    <tbody>
                      <td>dummy 1</td>
                      <td>dummy 1</td>
                      <td>dummy 1</td>
                    </tbody>
                  </table>
                  <br />
                  <div class="panel-info-item">
                      <NavLink to="/">
                    <a class="btn">
                      Back
                    </a>
                    </NavLink>
                  </div>
                </div>
              </div>
            </div>
          </section>
        </main>

        <footer>
          <div class="footer-content">
            <div>
              <button class="button">
                <div class="button-content">Official Channel</div>
                <div class="button-image">
                  <img src="/img/button.png" />
                </div>
              </button>
            </div>

            <div>
              <button class="button">
                <div class="button-content">Support Team</div>
                <div class="button-image">
                  <img src="/img/button.png" />
                </div>
              </button>
            </div>
          </div>
        </footer>
      </>
    )
  }
}

export default Tts
